import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { filter, map, switchMap } from 'rxjs/operators';
import { IPerson } from '../staff.model';
import { StaffService } from '../staff.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  public person: IPerson | undefined;
  constructor(
    private staffService: StaffService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params
      .pipe(
        filter((params: Params) => params.uid),
        map((params: Params) => params.uid),
        switchMap((uid: string) => this.staffService.getOne(uid))
      )
      .subscribe(
        (person: IPerson) => (this.person = person),
        (err)=> console.error(err)
      );
  }
}
