import { Injectable } from '@angular/core';
import { Observable, of, throwError, Subscriber } from 'rxjs';
import { staffs } from './mock';
import { IPerson } from './staff.model';
import * as uuid from 'uuid';

@Injectable({
  providedIn: 'root'
})
export class StaffService {
  private staff: IPerson[] = [...staffs];

  public getList(): Observable<IPerson[]> {
    return of(this.staff);
  }

  public getOne(uid: string): Observable<IPerson> {
    return new Observable((subscriber: Subscriber<IPerson>) => {
      const p = this.staff.find(person => person.id === uid);
      if (!p) {
        throw new Error(`Person ${uid} not Found`);
      }
      subscriber.next(p);
    });
  }

  public patchStaff(id: string, data: Partial<IPerson>): Observable<IPerson> {
    return new Observable((subscriber: Subscriber<IPerson>) => {
      const index = this.staff.findIndex(staff => staff.id === id);
      if (index === -1) {
        throw new Error('No such staff');
      }
      this.staff[index] = { ...this.staff[index], ...data };
      subscriber.next(this.staff[index]);
    });
  }

  public postStaff(person: IPerson): Observable<IPerson> {
    return new Observable((subscriber: Subscriber<IPerson>) => {
      person.id = uuid.v4();
      this.staff.push(person);
      subscriber.next(person);
    });
  }
}
