import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators
} from '@angular/forms';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { filter, map, switchMap } from 'rxjs/operators';
import { IPerson, JobPostion } from '../staff.model';
import { StaffService } from '../staff.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {
  public person: IPerson | undefined;
  public form: FormGroup;

  constructor(
    private staffService: StaffService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.form = this.fb.group({
      firstName: ['', [Validators.required]],
      lastName: ['', [Validators.required]],
      position: [JobPostion.employee, [Validators.required]]
    });
    this.activatedRoute.params
      .pipe(
        filter((params: Params) => params.uid),
        map((params: Params) => params.uid),
        switchMap((uid: string) => this.staffService.getOne(uid))
      )
      .subscribe(
        (person: IPerson) => {
          this.person = person;
          this.form.setValue({
            firstName: person.firstName,
            lastName: person.lastName,
            position: person.position
          });
        },
        err => console.error(err)
      );
  }

  get positions(): JobPostion[] {
    return [JobPostion.owner, JobPostion.employee, JobPostion.manager];
  }

  save() {
    if (this.form.valid) {
      const person = this.form.value;
      let action$: Observable<IPerson>;
      if (this.person) {
        action$ = this.staffService.patchStaff(this.person.id, person);
      } else {
        action$ = this.staffService.postStaff(person);
      }
      action$.subscribe((person: IPerson) => {
        this.router.navigate(['/staff/detail', person.id]);
      });
    }
  }
}
