import { Pipe, PipeTransform } from '@angular/core';
import { JobPostion } from '../staff.model';

@Pipe({
  name: 'positionToPrint'
})
export class PositionToPrintPipe implements PipeTransform {
  transform(value: JobPostion): string {
    if (value === JobPostion.owner) {
      return 'Owner';
    }
    if (value === JobPostion.manager) {
      return 'Manager';
    }
    if (value === JobPostion.employee) {
      return 'Staff';
    }
    return '';
  }
}
